import { Injector, OpaqueToken } from '@angular/core';
import { EffectsSubscription } from './effects-subscription';


export const /** @type {?} */ afterBootstrapEffects = new OpaqueToken('ngrx:effects: Bootstrap Effects');
/**
 * @param {?} injector
 * @param {?} subscription
 * @return {?}
 */
export function runAfterBootstrapEffects(injector: Injector, subscription: EffectsSubscription) {
  return () => {
    const /** @type {?} */ effectInstances = injector.get(afterBootstrapEffects, false);

    if (effectInstances) {
      subscription.addEffects(effectInstances);
    }
  };
}
